import { FILE_STATUS, FILE_STATUS_CLASS_NAME } from 'constants/file';
import { ConvertDate } from 'helpers/ConvertDate';
import moment from 'moment';
import React from 'react';
import { Link } from 'react-router-dom';

FileInfo.propTypes = {};

function FileInfo(props) {
    const { loading, file } = props;

    let finished_day = moment(file?.cast.accepted_at).add(file?.processing_time, 'h');

    return (
        <div className="col-md-6 mb-3 mb-xl-0">
            <div className="panel panel-info">
                <div className="panel-heading">
                    <h4 className="mb-0">Thng tin h s</h4>
                </div>
                <div className="panel-body">
                    {loading && <div className="sp sp-page sp-circle"></div>}
                    {!loading && (
                        <table className="table table-bordered">
                            <tbody>
                                <tr>
                                    <th style={{ width: '20%' }}>M h s</th>
                                    <td>{file.key}</td>
                                </tr>
                                <tr>
                                    <th>Thuc th tc</th>
                                    <td>
                                        {file.procedure.name}
                                        <Link className="btn btn-sm btn-primary pull-right" to={`/chi-tiet-thu-tuc/${file.procedure.key}`}>
                                            Xem th tc
                                        </Link>
                                    </td>
                                </tr>
                                <tr>
                                    <th>Trng thi</th>
                                    <td>
                                        <div className={`badge badge-pill badge-${FILE_STATUS_CLASS_NAME[file.status]}`}>
                                            {FILE_STATUS[file.status]}
                                        </div>
                                    </td>
                                </tr>
                                <tr>
                                    <th>Thi gian x l</th>
                                    <td>{ConvertDate(file.processing_time)}</td>
                                </tr>
                                <tr>
                                    <th>Ngy np</th>
                                    <td>{file.cast.created_at.format('hh:mm:ss DD-MM-YYYY')}</td>
                                </tr>
                                <tr>
                                    <th>Ngy tip nhn</th>
                                    <td>{file.status > 0 ? file.cast.accepted_at.format('hh:mm:ss DD-MM-YYYY') : `Cha tip nhn`}</td>
                                </tr>
                                <tr>
                                    <th>Ngy tr d kin</th>
                                    <td>{file.status > 0 ? finished_day.format('hh:mm:ss DD-MM-YYYY') : `Cha tip nhn`}</td>
                                </tr>
                                <tr>
                                    <th>Ngy tr</th>
                                    <td>{file.status > 1 ? file.cast.finished_at.format('hh:mm:ss DD-MM-YYYY') : `Cha hon thnh`}</td>
                                </tr>
                            </tbody>
                        </table>
                    )}
                </div>
            </div>
        </div>
    );
}

export default FileInfo;
