import Swal from 'sweetalert2';
import './SweetAlert.scss';

const SweetAlert = {
    success(message) {
        Swal.fire({
            icon: 'success',
            title: 'Thnh cng',
            html: message,
        });
    },

    successCallback(message, callback) {
        Swal.fire({
            title: 'Thnh cng',
            html: message,
            icon: 'success',
            confirmButtonText: 'ng ',
        }).then((result) => {
            if (result.isConfirmed) {
                callback();
            }
        });
    },

    warningCallback(message, callback) {
        Swal.fire({
            title: 'Cnh bo',
            html: message,
            icon: 'warning',
            confirmButtonText: 'ng ',
        }).then((result) => {
            if (result.isConfirmed) {
                callback();
            }
        });
    },

    error(message) {
        Swal.fire({
            icon: 'error',
            title: 'Tht bi!',
            html: message,
        });
    },

    confirm(message, callback, icon = 'warning', title = 'Cnh bo!') {
        Swal.fire({
            icon: icon,
            title: title,
            html: message,
            showCancelButton: true,
            backdrop: 'rgba(0,0,0,0.4)',
            confirmButtonText: 'ng ',
            confirmButtonColor: '#AD4105',
            cancelButtonText: 'Hy',
        }).then((result) => {
            if (result.isConfirmed) {
                callback();
            }
        });
    },
};

export default SweetAlert;
