import { unwrapResult } from '@reduxjs/toolkit';
import { loading } from 'components/common/Loading/loadingSlice';
import SweetAlert from 'lib/SweetAlert';
import React from 'react';
import { useDispatch } from 'react-redux';
import { Link } from 'react-router-dom';
import RegisterForm from './RegisterForm';
import { register } from './userSlice';

Register.propTypes = {};

function Register(props) {
    const dispatch = useDispatch();

    const handleSubmit = async (data) => {
        dispatch(loading(true));
        try {
            console.log('datav', data);
            const resultAction = await dispatch(register(data));
            unwrapResult(resultAction);
            SweetAlert.success('ng k ti khon thnh cng.');
        } catch (error) {
            SweetAlert.error(error.message);
        }
        dispatch(loading(false));
    };
    return (
        <div className="container">
            <div className="content">
                <div className="content-container">
                    <div className="content-header">
                        <h2 className="content-header-title">NG K TI KHON</h2>
                    </div>

                    <div className="row">
                        <div className="col-md-12">
                            <RegisterForm onSubmit={handleSubmit} />
                            <div className="row">
                                <div className="col-md-12">
                                    <div className="row m-3">---- hoc ----</div>
                                    <div className="form-group">
                                        <Link to="/login" className="btn btn-danger mr-3" role="button">
                                            ng nhp
                                        </Link>
                                        <Link to="/" className="btn btn-warning" role="button">
                                            Qun mt khu?
                                        </Link>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Register;
