import { yupResolver } from '@hookform/resolvers/yup';
import Address from 'api/Address';
import DatePicker from 'components/common/FormControls/DatePicker';
import InputField from 'components/common/FormControls/InputField';
import PasswordField from 'components/common/FormControls/PasswordField';
import Select from 'components/common/FormControls/Select';
import FullnameValidate from 'components/common/Validations/Fullname';
import PhoneNumberValidate from 'components/common/Validations/PhoneNumber';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import * as yup from 'yup';

RegisterForm.propTypes = {
    onSubmit: PropTypes.func,
};

function RegisterForm(props) {
    const schema = yup.object().shape({
        email: yup.string().required('Email khng c  trng').email('Email khng hp l'),
        full_name: yup.string().required('H tn khng c b trng').test(FullnameValidate),
        identity_card: yup.string().required('S chng minh nhn dn khng c b trng'),
        identity_card_date: yup.string().required('Ngy cp khng c b trng'),
        identity_card_address: yup.string().required('Ni cp khng c b trng'),
        date_of_birth: yup.string().required('Ngy sinh khng c b trng'),
        phone_number: yup
            .string()
            .required('S in thoi khng c b trng')
            .matches(...PhoneNumberValidate),
        province: yup.string().required('Tnh/Thnh ph khng c b trng'),
        district: yup.string().required('Qun/Huyn khng c b trng'),
        ward: yup.string().required('X/Phng/Th trn khng c b trng'),
        password: yup.string().required('Mt khu khng c b trng').min(6, 'Mt khu t nht 6 k t'),
        re_password: yup
            .string()
            .required('Mt khu xc nhn khng c b trng')
            .oneOf([yup.ref('password')], 'Mt khu khng trng khp'),
    });

    const form = useForm({
        defaultValues: {
            email: '',
            full_name: '',
            identity_card: '',
            identity_card_date: new Date(),
            identity_card_address: '',
            date_of_birth: new Date(),
            phone_number: '',
            password: '',
            re_password: '',
        },
        resolver: yupResolver(schema),
    });

    const [provinces, setProvinces] = useState([]);
    const [districts, setDistricts] = useState([]);
    const [wards, setWards] = useState([]);

    useEffect(() => {
        (async () => {
            try {
                const list = await Address.getProvince();
                setProvinces(
                    list.map((item) => ({
                        label: item.name,
                        value: item.id,
                    }))
                );
            } catch (error) {}
        })();
    }, []);

    const onSubmit = async (data) => {
        const { onSubmit } = props;
        if (onSubmit) {
            await onSubmit(data);
        }
    };

    const handleChangeProvince = async (value) => {
        try {
            setDistricts([]);
            const list = await Address.getDistrict(value);
            setDistricts(
                list.map((item) => ({
                    label: item.name,
                    value: item.id,
                }))
            );
        } catch (error) {}
    };

    const handleChangeDistrict = async (value) => {
        try {
            setWards([]);
            const list = await Address.getWard(value);
            setWards(
                list.map((item) => ({
                    label: item.name,
                    value: item.id,
                }))
            );
        } catch (error) {}
    };

    const handleFormReset = () => {
        form.reset();
        setDistricts([]);
        setWards([]);
    };

    return (
        <form onSubmit={form.handleSubmit(onSubmit)}>
            <div className="row">
                <InputField col={4} label="H tn" name="full_name" form={form} required />
                <InputField col={4} label="Tn c quan t chc" name="organization" form={form} />
                <DatePicker col={4} label="Ngy sinh" name="date_of_birth" form={form} required />
            </div>

            <div className="row">
                <InputField col={4} label="S CMND" name="identity_card" form={form} required />
                <DatePicker col={4} label="Ngy cp CMND" name="identity_card_date" form={form} required />
                <InputField col={4} label="Ni cp CMND" name="identity_card_address" form={form} required />
            </div>

            <div className="row">
                <InputField col={4} label="S in thoi" name="phone_number" form={form} required />
                <InputField col={4} label="S fax" name="fax" form={form} />
                <InputField col={4} label="Website" name="website" form={form} />
            </div>

            <div className="row">
                <Select
                    col={4}
                    label="Tnh/Thnh ph"
                    name="province"
                    options={provinces}
                    onChange={handleChangeProvince}
                    form={form}
                    required
                    loading={!provinces.length}
                />
                <Select
                    col={4}
                    label="Qun/Huyn"
                    name="district"
                    options={districts}
                    onChange={handleChangeDistrict}
                    form={form}
                    required
                    loading={!districts.length}
                />
                <Select col={4} label="X/Phng/Th trn" name="ward" options={wards} form={form} required loading={!wards.length} />
            </div>

            <div className="row">
                <InputField col={4} label="Email" name="email" form={form} required />
                <PasswordField col={4} label="Mt khu" name="password" form={form} required />
                <PasswordField col={4} label="Xc nhn mt khu" name="re_password" form={form} required />
            </div>

            <div className="form-group">
                <button type="submit" className="btn btn-info">
                    ng k
                </button>
                <button type="button" className="btn btn-default ml-3" onClick={handleFormReset}>
                    Lm mi
                </button>
            </div>
        </form>
    );
}

export default RegisterForm;
