import PropTypes from 'prop-types';
import React from 'react';
import { Link } from 'react-router-dom';

FileResult.propTypes = {
    file: PropTypes.object.isRequired,
};

function FileResult(props) {
    const { file } = props;

    return (
        <div>
            <div className="panel panel-success">
                <div className="panel-heading text-center">
                    <h4>Thnh cng</h4>
                </div>
                <div className="panel-body">
                    <div className="text-center text-success">
                        <h3> np h s thnh cng</h3>
                    </div>
                    <div className="text-danger">
                        Vui lng lu li nhng thng tin sau  d dng cho vic tra cu v chnh sa h s (Thng tin ny ch c cp 1 ln duy
                        nht)
                    </div>
                    <div className="table-responsive mt-3">
                        <table className="table table-bordered table-hover">
                            <tbody>
                                <tr>
                                    <th style={{ width: '18%' }}>M h s</th>
                                    <td>{file.key}</td>
                                </tr>
                                {/* <tr>
                                    <th>M b mt ( sa h s)</th>
                                    <td>
                                        <b className="text-danger">{file.access_key}</b>
                                    </td>
                                </tr> */}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>

            <div className="row mt-3">
                <div className="col-md-12 text-center">
                    <Link to="/" role="button" className="btn btn-success">
                        <i className="fa fa-home"></i>&nbsp;&nbsp;V trang ch
                    </Link>
                </div>
            </div>
        </div>
    );
}

export default FileResult;
