import toastr from 'toastr';
import 'toastr/toastr.scss';
import './Toastr.scss';

const Toastr = {
    success(message) {
        toastr.success(message, 'Thnh cng', { "timeOut": 2000 });
    },

    error(message) {
        toastr.error(message, 'Tht bi', { "timeOut": 2000 });
    },
};

export default Toastr;
